<template>
  <div class="Slider">
    <div class="S_cont" ref="S">
      <ul class="S_list" ref="slider">
        <li class="S_el" v-for="item in images" :key="item.id">
          <img :src="require(`@/assets/images/gallery/${item.img}`)">
        </li>
      </ul>
      <div class="index" :class="{blur : options}">{{active_Index}}</div>
    </div>

    <div class="btn_box" :class="{blur : options}">
      <div class="prev" ref="prev" @click="prev">◄</div>
      <div class="next" ref="next" @click="next">►</div>
    </div>
    
  </div>
</template>

<script>
  export default{
    data(){
      return{
        active_Index: Number,
      }
    },
    props:{
      images:Array,
      index: Number,
      options: Boolean,
    },
    methods:{
      next(){
        if(this.active_Index <= this.images.length -1){
          this.active_Index++;
        }
      },
       prev(){        
        if(this.active_Index > 1 ){
          this.active_Index--;
        }
      },
    },
    mounted(){    
    },
    watch:{
      index(){
        this.active_Index = this.index+1;
      },
      active_Index(){
        const slider = this.$refs.slider;
        const next = this.$refs.next;
        const prev = this.$refs.prev;
        slider.style.transform = `translateX(${(this.active_Index-1) * -100}%)`;

        if(this.active_Index == 1){
          prev.classList.add('max')
        }else{
          prev.classList.remove('max')
        }
        if(this.active_Index == this.images.length){
          next.classList.add('max')
        }else{
          next.classList.remove('max')
        }
      }
    }

  }
</script>

<style scoped>


  .Slider{
    display: flex;
    justify-content: center;align-items: center;
    width: 100%; height: 100%;
  }

  .S_cont{
    max-width: 800px;
    max-height: 400px;
    border:1px solid #fff;
    margin: 100px 1em;
    position: relative;
    overflow:hidden;
    transition:0.5s;
  }

  .S_list{
    display: flex;
    width: 100%; height: 100%;
    transition: 0.5s;
  }

  .S_el{
    min-width: 100%;
    min-height: 100%;
  }

  img{
    width: 100%; height: 100%;
    object-fit: cover;
    user-select: none; pointer-events: none;
  }


  .btn_box{
    position: absolute; top: 0px;left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    transition: 0.3s;
    z-index: 10000;
  }


  @media (max-width: 600px) {
    .btn_box{
      top: auto;
      bottom: 100px;
    }
  }

  .next, .prev{
    transition: 0.3s;
    user-select: none;
    cursor: pointer;
    border:1px solid #fff;
    padding: 5px 20px;
  }
  .next:hover, .prev:hover{
    color:#206C06;
    border-color: #206C06;
    background-color: #2CAE39;

  }

  .max{
    pointer-events: none;
    opacity: 0.3;
  }

    .index{
      position: absolute;
      z-index: 100;
      user-select: none;
      top: 0.3em;left: 0.3em;
      border:0.08em solid #fff;
      padding: 0.2em 0.8em;
      backdrop-filter: blur(5px);
      background-color: rgba(22,22,22, 0.7);
      border-radius: 0.5em;
      transition: 0.3s;
    }


    .blur{
      opacity: 0;
      pointer-events: none;
    }


</style>