<template>
<div class="Gallery">
  <gItem
    v-for="(item, i) in images" :key="item.id"
    :index="i" :image_el="item"
    @modal-active="modalActive"
    @index-el="indexEl"
  />
</div>  
</template>



<script>
  import gItem from './G_item.vue';

  export default{
   props:{
    images: Array,
   },
    components:{
      gItem,
    },
    methods:{
      modalActive(e){
        this.$emit('modal-active', e);
      },
      indexEl(e){
        this.$emit('index-el', e);
      }
    },
  }

</script>

<style>



  .Gallery{
    width: 90%; height: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1em;
  }


  /*media*/

  @media (max-width: 800px) {
    
    .Gallery{grid-template-columns: repeat(3, 1fr); width: 95%;}
  }

  @media (max-width: 500px) {
    
    .Gallery{grid-template-columns: repeat(2, 1fr); width: 98%;}
  }
  
</style>