<template>
<div class="gallery-item" 
    @click="modalActive">
  <img :src=" require(`@/assets/images/gallery/${image_el.img}`)">
</div>  
</template>



<script>
  export default{
    props:{
      image_el:Object,
      index: Number,
    },
    components:{

    },
    methods:{
      modalActive(){        
        this.$emit('modal-active', true);
        this.$emit('index-el', this.index);
      }
    }

  }

</script>


<style scoped>
  
  .gallery-item{
    box-shadow: 0px 0px 5px 2px #dcdde1;
    border:0.1em solid #dcdde1;
    border-radius: 0.2em;
    cursor: pointer;
    transition: 0.5s;
    box-sizing: border-box;
    overflow: hidden;
    height: 150px;
    position: relative;
  }

  .gallery-item:hover img{
    width: 100%; height: 100%;
    filter: blur(0px);
  }
  .gallery-item:hover{
    box-shadow: 0px 0px 12px 6px #fbc531;
    border:0.1em solid #fbc531;
  }



  img{
    position: absolute;
    top: 50%;left: 50%;
    object-fit: cover;
    transform: translate(-50%, -50%);
    width: 135%; height: 135%;
    transition: 0.35s;
    filter: blur(1px);
  }


  /*media*/


  @media (max-width: 800px) {
    
     img{
        filter: blur(0px);
      }
  }

</style>