<template>
  <div id="app">
    <h1 class="main-title">app gallery</h1>
    <Gallery
      @modal-active="modalActive" 
      @index-el="indexEl"
      :images="dataImg"
    />
    <Modal
      :modal_window="modal"
      :images="dataImg"
      :index="el_index"
      @remove-modal="modalRemove"
    />
  </div>
</template>

<script>

  import Gallery from '@/components/Gallery.vue';
  import Modal from '@/components/Modal.vue';

  export default {
    name: 'App',
    data(){
      return{
        modal: false,
        el_index: 0,
        dataImg:[
          {id: 1,name: 'img_1' , img: '1.jpg'},
          {id: 2,name: 'img_2' , img: '2.jpg'},
          {id: 3,name: 'img_3' , img: '3.jpg'},
          {id: 4,name: 'img_4' , img: '4.jpg'},
          {id: 5,name: 'img_5' , img: '5.jpg'},
          {id: 6,name: 'img_6' , img: '6.jpg'},
          {id: 7,name: 'img_7' , img: '7.jpg'},
          {id: 8,name: 'img_8' , img: '8.jpg'},
          {id: 9,name: 'img_9' , img: '9.jpg'},
          {id: 10,name: 'img_10' , img: '10.jpg'},
          {id: 11,name: 'img_11' , img: '11.jpg'},
          {id: 12,name: 'img_12' , img: '12.jpg'},
          {id: 13,name: 'img_13' , img: '13.jpg'},
          {id: 14,name: 'img_14' , img: '14.jpg'},
        ],
      }
    },
    components: {
      Gallery, Modal
    },
    methods:{
      modalActive(e_true){
        this.modal = e_true;
      },
      modalRemove(e_false){
        this.modal = e_false;
      },
      indexEl(index){
        this.el_index = index;
      }
    },
    watch:{
      modal(){
        const body = document.querySelector('body');

        if(this.modal == true){
          body.classList.add('body_hidden')
        }else{
          body.classList.remove('body_hidden');
        }
      }
    }
  }
</script>

