<template>
  <div class="Modal" :class="{active : modal_window}">
      <i class=" remove icon-cancel-circle " @click="removeModal"></i>
      <Slider
        :images="images"
        :index="index"
        :options="options"
      />
      <Options
        @open-option="openOption"
        :option="options"
      />
  </div>
</template>


<script>
  import Slider from './modal_components/slider.vue'; 
  import Options from './modal_components/opitions.vue'; 


  export default {
    data(){
      return{
        options: false,
      }
    },
    props:{
      modal_window: Boolean,
      images: Array,
      index:Number,
    },
    components:{
      Slider, Options
    },
    methods:{
      removeModal(){
        this.$emit('remove-modal', false);
        this.options = false;
      },
      openOption(e){
        this.options = e;
      }
    },
    mounted(){
    }
  }
</script>

<style scoped>
  .Modal{
    overflow:auto;
    position: fixed;
    transition: 0.5s;
    top: 0;left: 0;
    width: 100%; height: 100%;
    background: rgba(0,0,0,0.95);
    opacity: 0;
    transform: translateY(-110%);
    pointer-events: none;
  }

  .active{
    transform: translateY(0);
    opacity: 1;
    pointer-events: auto;
  }

  .remove{
    position: absolute;
    top: 15px;right: 15px;
    cursor: pointer;
    transition: 0.3s;
    font-size: 2em;
    padding: 10px;
  }

  .remove:hover{
    color: #c23616;
  }
</style>