<template>
  <div class="Options">
    <i class="O_btn icon-cog" :class="{rotate : option}" @click="opitions"></i>
    <ul class="O_list" :class="{active : option}">
      <li ref="L_deg" class="O_el left icon-spin"></li>
      <li ref="R_deg" class="O_el icon-spin"></li>
      <li ref="Z_in" class="O_el icon-zoom-in"></li>
      <li ref="Z_out" class="O_el icon-zoom-out"></li>
      <li class="O_el icon-download"></li>
    </ul>    
  </div>
</template>

<script>
  export default{
    data(){
      return{
        deg:0,
        scale:1,
      }
    },
    props:{
      option:Boolean,
    },
    methods:{
      opitions(){
        this.$emit('open-option', !this.option)
      }
    },
    mounted(){
      const S_cont = document.querySelector('.S_cont');
      /*_______________________*/
      const L_deg = this.$refs.L_deg;
      const R_deg = this.$refs.R_deg;
      /*________________*/
      const Z_in = this.$refs.Z_in;
      const Z_out = this.$refs.Z_out;
      /*_____________*/


      L_deg.addEventListener('click', ()=>{
        S_cont.style.transform = `rotate(${this.deg-=90}deg)`;
      });
      R_deg.addEventListener('click', ()=>{
        S_cont.style.transform = `rotate(${this.deg+=90}deg)`;
      });

      /*_______________________________________________*/

      Z_in.addEventListener('click', ()=>{
        if(this.scale <= 2){
          S_cont.style.transform = `scale(${this.scale+=0.2})`;
        }
      });

      Z_out.addEventListener('click', ()=>{
        if(this.scale > 0.4){
          S_cont.style.transform = `scale(${this.scale-=0.2})`;
        }
      });

    },
    watch:{
      option(){
        const S_cont = document.querySelector('.S_cont');

        if(this.option == false){
          this.deg = 0;
          S_cont.style.transform = `rotate(${this.deg}deg)`;
        }
        if(this.option == false){
          this.scale = 1;
          S_cont.style.transform = `scale(${this.scale})`;
        }
      }
    }
  }
</script>


<style>
  .Options{
    position: absolute;
    top: 1em; left: 1em;
    display: flex;

  }

  @media (max-width: 600px) {
    .Options{
      top: auto;
      bottom: 1.5em;
    }
  }



  .O_list{position: relative;
    display: flex;
    transition: 0.5s;
    transform: scaleX(0) translateX(-30%);
    opacity: 0;
    transform-origin: left center;
  }

  .active{    
    transform: scaleX(1) translateX(-20px);
    opacity: 1;
  }

  .O_btn{    
    position: relative;
    z-index: 100;
    padding: 10px;
    cursor: pointer;
    background-color: #fff;
    color:#222;
    border-radius: 50%;
    transition: 0.5s;
  }
  .rotate{    
    transform: rotate(145deg);
  }

  .O_el{
    padding: 10px;
    cursor: pointer;
    transition: 0.3s;
    background-color: #fff;
    color:#222;
  }

  .O_el:last-child{    
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
  }

  .O_el:first-child{    
    padding-right: 30px;
  }

  .O_el:hover{
    color: #c23616;
  }


  .left{
    transition: 0.5s;
    transform: scaleX(-1);
  }

</style>